<template>
    <div>
        <div class="groupBtn">
            <!--matterBtn-->
            <el-row :gutter="30" class="matterBtnToNext" v-show="isMatter">
                <el-col :xs="12" :sm="8" v-for="(item,index) in matterList" :key="index">
                    <router-link :to="toNext">
                        <el-button-group>
                            <el-button :type="((item.name==curMatter) ? 'warning' : 'primary')" @click="matterBtnClick(item.name)" >
                                <p>{{item.name}}</p>
                            </el-button>
                        </el-button-group>
                    </router-link>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>

export default {
  name: 'ServiceMatter',
  data () {
    return {
      isMatter: true,
      matterList: [],
      curTheme: '',
      curMatter: '',
      toNext: '/reference/element'
    }
  },
  watch: {

  },
  created: function () {
    let self = this
    if (sessionStorage.reference) {
      let obj = JSON.parse(sessionStorage.reference)
      this.curMatter = obj.curMatter
      let curTheme = obj.curTheme
      this.curTheme = curTheme
      if (curTheme) {
        this.getMatter()
      } else {
        self.isMatter = false
        self.$alert('未选择主题', '警告', {
          confirmButtonText: '确定',
          callback: action => {
            // self.$router.push({path: 'reference/ServiceTheme'})
          }
        })
      }
    } else {
      self.isMatter = false
      self.$alert('未选择主题', '警告', {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    }
  },
  mounted: function () {
  },
  methods: {
    async getMatter () {
      try {
        let params = {
          subject: this.curTheme
        }
        let res = await this.$api.unsplash.getMatters(params)
        if (res.errno === '0') {
          this.isMatter = true
          this.matterList = res.data.items
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    matterBtnClick (matterName) {
      // save
      var obj = JSON.parse(sessionStorage.reference)
      obj.curMatter = matterName
      // 只要点击就清除selectionElementId
      obj.selectionElementId = []
      obj.allID = []
      sessionStorage.reference = JSON.stringify(obj)
      // console.log(this.qs.stringify(sessionStorage.reference))
    },
    notifyFn (title, msg, type = 'warning') {
      this.$notify({
        title: title,
        message: msg,
        type: type,
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    }
  }
  // beforeRouteLeave (to, from, next) {
  //   // 设置下一个路由的 meta
  //   to.meta.keepAlive = false // 刷新
  //   next()
  // }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .groupBtn{
        margin-top: 40px;
        padding: 0 20px 0 20px;
        position: relative;
        .matterBtnToNext{
            .el-col{
                margin-bottom: 3vh;
                .el-button-group{
                    height: 10vh;
                    width: 100%;
                    .el-button{
                        height: 10vh;
                        width: 100%;
                        padding: 4px 8px 4px 8px;
                        font-size:22px;
                        line-height: 24px;
                        p{
                            word-wrap: break-word !important;
                            word-break: break-all !important;
                            white-space: normal !important;
                            margin: 0;
                        }
                    }
                }
            }
        }
    }

</style>



